.display1{
  font-size: 3.125em;
  line-height: 138%;
}

.text65{
  font-size: 4.0625em;
  line-height: 120%;
  letter-spacing: -0.045em;
}

.text27{
  font-size: 1.6875em;
  line-height: 120%;
  letter-spacing: -0.045em;
}

.text20{
  font-size: 1.25em;
  line-height: 140%;
}

.text16{
  font-size: 1em;
  line-height: 137.5%;
}

.text14{
  font-size: .875em;
  line-height: 133%;
}

.bold-text{
  font-weight: 700;
}

.gray-text{
  color: rgba(#000,.4);
}

.text-center{
  text-align: center;
}


@media screen and (max-width: 992px){
  .text65{
    font-size: 2.625em;
  }

  .text27{
