.article{
  padding-bottom: 6.25em;
  flex-grow: 1;
}

.article-top{
  padding: .5em 0;
  margin-bottom: 1.5em;
  border-bottom: 1px solid #000000;
  display: flex;
  align-items: center;
  justify-content: space-between;
  &__title{
    font-size: 1.375em;
    line-height: 120%;
    font-weight: 700;
    span{
      color: $primary;
      text-transform: uppercase;
    }
  }
}

.article-socials{
  display: flex;
  align-items: center;
  flex-shrink: 0;
  &__item{
    display: flex;
    align-items: center;
    padding: 0 .625em;
    border: 1px solid $black;
    height: 1.6875em;
    border-radius: 3.4375em;
    &:not(:last-child){
      margin-right: .75em;
    }
    img{
      max-width: 1.25em;
      max-height: .9375em;
    }
    &:hover{
      border-color: $primary;
    }
  }
}

.article-text{
  margin-top: 1.5em;
  img{
    width: 100%;
    &:not(:last-child){
      margin-bottom: 2em;
    }
  }
  p,h3{
    font-size: 1.5625em;
    line-height: 136%;
    &:not(:last-child){
      margin-bottom: 1.25em;
    }
  }
  b,
  h3{
    font-weight: 700;
  }
}

.article-bottom{
  padding: .5em 0;
  margin-top: 2.5em;
  border-bottom: 1px solid #000000;
}

.article-nav{
  margin-top: 3.125em;
  display: grid;
  grid-template-columns: 1fr 1fr;
  &__btn{
    height: 3.4375em;
    padding: 0 1.5em;
    span{
      font-size: 1.5em;
    }
    svg{
      width: .875em;
      height: auto;
    }
    &._prev{
      justify-content: unset;
      svg{
        margin-right: .625em;
      }
    }
    &._next{
      justify-content: flex-end;
      svg{
        margin-left: .625em;
      }
    }
    &:hover{
      path{
        fill: $black
      }
    }
  }
}


@media screen and (max-width: 576px){
  .article{
    padding-bottom: 3.125em;
    &__title{
      font-size: 1.5625em;
      line-height: 111%;
    }
  }

  .article-top{
    margin-bottom: 2em;
    align-items: flex-end;
    &__title{
      font-size: 1em;
      span{
        font-size: .875em;
        display: block;
      }
    }
  }

  .article-text{
    p,h3{
      font-size: 1em;
    }
    h3{
      margin-top: 2em;
      &:not(:last-child){
        margin-bottom: 2em;
      }
    }
  }

  .article-bottom{
    margin-top: 1.5em;
  }

  .article-nav{
    margin-top: 2em;
    &__btn{
      height: 3.125em;
      span{
        font-size: 1em;
      }
    }
  }
}
