@import 'vars';
html {
    width: 100%;
    font-size: unquote("min(100vw, 1260px)");
}

.wrapper {
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    flex-shrink: 0;
}

.content {
    flex-grow: 1;
    display: flex;
    flex-direction: column;
}

.page {
    font-family: $font1;
    font-weight: 400;
    color: $black;
    font-size: 1.26984%;
    &.fixed-body {
        overflow-y: hidden;
    }
}

.container {
    max-width: 1260px;
    margin: 0 auto;
    padding: 0 .625em;
}

a {
    &:hover {
        color: $primary;
    }
}

@media screen and (max-width: 992px){
    .page{
        font-size: 1.6129%;
    }
}


@media screen and (max-width: 768px){
    .page{
        font-size: 2.08333%;
    }
}


@media screen and (max-width: 576px){
    .page{
        font-size: 4%;
    }
}
