.fg {
  position: relative;
  margin: 0;
  input {
    border-radius: .1875em;
    border: 1px solid #000000;
    padding: 0 .625em;
    height: 2.5em;
    width: 100%;
    font-size: 1em;
    background-color: transparent;
    color: $black;
    &::placeholder {
      color: rgba($black,.5);
    }
  }
}

.check {
  position: relative;
  input {
    position: absolute;
    opacity: 0;
    width: 100%;
    height: 100%;
    left: 0;
    top: 0;
    z-index: 2;
    cursor: pointer;
    &:checked+label {
      &::before {
        // background-color: $green;
        border: 3px solid #fff;
      }
    }
    &:disabled+label {
      color: #AAAAAA;
      pointer-events: none;
    }
  }
  label {
    padding-left: 40px;
    position: relative;
    min-height: 20px;
    font-size: 14px;
    line-height: 20px;
    cursor: pointer;
    &:before {
      content: '';
      position: absolute;
      z-index: 1;
      border-radius: 2px;
      background: rgba(255, 255, 255, 0.01);
      border: 1px solid rgba(255, 255, 255, 0.45);
      width: 20px;
      height: 20px;
      left: 0;
      top: 50%;
      transform: translateY(-50%);
      transition: all .100s;
    }
  }
}

.rating-input {
  display: flex;
  align-items: center;
  flex-direction: row-reverse;
  position: relative;
  &--read-only {
    pointer-events: none;
  }
  &__field{
    position: absolute;
    left: 0;
    top: 100%;
    opacity: 0;
    pointer-events: none;
  }
  &__item {
    transition: all .3s;
    width: 1em;
    svg{
      width: 100%;
      height: auto;
    }
    path{
      fill: rgba($black,.5);
    }
    &:not(:last-child){
      margin-right: .125em;
    }
    &:hover {
      cursor: pointer;
      path{
        fill: #FFE600;
      }
      &~.rating-input__item {
        path{
          fill: #FFE600;
        }
      }
    }
  }
}

.rating-input[data-total-rating="1"] .rating-input__item:nth-child(n+5),
.rating-input[data-total-rating="2"] .rating-input__item:nth-child(n+4),
.rating-input[data-total-rating="3"] .rating-input__item:nth-child(n+3),
.rating-input[data-total-rating="4"] .rating-input__item:nth-child(n+2),
.rating-input[data-total-rating="5"] .rating-input__item:nth-child(n+1) {
  path{
    fill: #FFE600;
  }
}
