@import "../mixins";
.footer{
  padding: 2.5em 0;
  background: $black;
  color: #FFFFFF;
  .container{
    max-width: 1800px;
  }
  &__inner{
    display: flex;
    align-items: flex-end;
    justify-content: space-between;
  }
  &__right{
    width: 25%;
  }
  &__left{
    display: grid;
    grid-template-columns: 1.2fr .9fr .9fr 1fr;
    gap: 1.625em 1.5em;
    align-items: center;
    max-width: 74em;
  }
  &__title{
    font-size: 2.5em;
    line-height: 100.5%;
    font-weight: 700;
    letter-spacing: -0.045em;
    grid-column: 1/4;
  }
  &__nav{
    @media screen and (min-width: 993px){
      grid-column: 4/5;
      grid-row: 1/3;
    }
  }
  &__images{
    display: flex;
    align-items: center;
    justify-content: space-between;
    max-width: 15em;
  }
  &__img{
    width: 4.625em;
    display: block;
  }
  &__links{
    display: flex;
    align-items: center;
    flex-direction: column;
  }
  &__link{
    margin: 1em auto 0 0;
    font-size: 1em;
    line-height: 137.5%;
  }
  a.footer__link{
    &:hover{
      color: $primary2;
    }
  }
}

.follow{
  &__items{
    display: flex;
    align-items: center;
    flex-wrap: wrap;
  }
  &__item{
    padding: 0 .625em;
    height: 1.6875em;
    border: 1px solid #fff;
    border-radius: .8125em;
    display: flex;
    align-items: center;
    margin: .75em .75em 0 0;
    img{
      max-height: .6875em;
    }
    &:hover{
      border-color: $primary2;
    }
  }
}

.subscribe{
  margin-top: .75em;
  &__form{
    margin-top: .75em;
    display: flex;
    .fg{
      flex-grow: 1;
      input{
        color: #fff;
        border-color: #fff;
        &::placeholder {
          color: rgba(#fff,.5);
        }
      }
    }
  }
  &__btn{
    padding: 0 .625em;
    height: 2.5em;
    margin-left: .25em;
    flex-shrink: 0;
    &:hover{
      background: #fff;
      color: $black;
    }
    span{
      font-size: 1em;
    }
  }
}


@media screen and (max-width: 1600px){
  .footer{
    &__inner{
      @include grid100gap(3.125em);
    }
    &__left{
      max-width: 100%;
    }
    &__right{
      width: 100%;
      display: grid;
      grid-template-columns: repeat(2,1fr);
      gap: 3.125em;
    }
  }

  .subscribe{
    margin: 0;
  }
}


@media screen and (max-width: 992px){
  .footer{
    &__left{
      grid-template-columns: 1fr 1fr 1fr 1.3fr;
      gap: 1.6875em 2em;
      align-items: unset;
    }
    &__title{
      grid-column: 1/-1;
    }
    &__images{
      padding: 2em 0;
      height: 8.6875em;
      max-width: 11.25em;
    }
    &__img{
      width: auto;
      max-width: 4.6875em;
      max-height: 100%;
    }
  }
}


@media screen and (max-width: 768px){
  .footer{
    @media screen and (min-width: 577px){
      &__images{
        position: absolute;
        right: 0;
        bottom: 0;
        align-items: flex-end;
        height: unset;
        padding: 0;
        width: 12.5em;
      }
      &__img{
        max-height: unset;
      }
    }
    &__inner{
      gap: 0;
      position: relative;
    }
    &__left{
      grid-template-columns: 1fr 1fr 1.3fr;
      gap: 2em;
    }
    &__right{
      width: 25em;
      max-width: 100%;
      grid-template-columns: 100%;
      gap: .75em;
    }
    &__title{
      font-size: 1.875em;
    }
  }
}


@media screen and (max-width: 576px){
  .footer{
    padding: 1.5em 0 3em;
    &__inner{
      gap: 2.5em;
    }
    &__left{
      grid-template-columns: 1fr 1fr;
      gap: 2.5em 1em;
    }
    &__title{
      font-size: 1.5625em;
    }
    &__subtitle{
      font-size: 1em;
      line-height: 137.5%;
    }
    &__nav{
      grid-column: 1/-1;
      grid-row: 2/3;
      max-width: 16.5em;
    }
    &__link{
      font-size: .875em;
      margin-top: .71em;
    }
    &__images{
      order: 10;
      width: 100%;
      max-width: 100%;
      padding: 0;
      height: unset;
    }
    &__img{
      max-width: 3.4375em;
      margin-right: 2.5em;
    }
  }

  .follow{
    &__items{
      width: calc(100% + .3125em);
    }
    &__item{
      margin-right: .3125em;
    }
  }

  .subscribe{
    &__form{
      @include grid100gap(.625em);
    }
    &__btn{
      margin: 0;
    }
  }
}
