@import "../mixins";
.header{
  background-color: #fff;
  padding: 1.375em 0;
  position: relative;
  z-index: 5;
  &__inner{
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &__logo{
    display: block;
    width: 5.625em;
    flex-shrink: 0;
    img{
      width: 100%;
      display: block;
    }
  }
  &__right{
    display: flex;
    align-items: center;
    justify-content: space-between;
    flex-grow: 1;
    margin-left: 6.25em;
  }
  &__buttons{
    display: flex;
    align-items: center;
  }
  &__btn{
    @include center;
    margin-left: 1.5em;
    width: 1.5em;
    flex-shrink: 0;
    &:hover{
      img{
        transform: translateY(-50%);
      }
    }
    img{
      width: 100%;
      transition: all .3s;
    }
  }
}

.nav{
  display: flex;
  align-items: center;
  &__link{
    margin-right: 2.75em;
    line-height: 140%;
    font-weight: 700;
    position: relative;
    transition: all .5s;
    span{
      position: relative;
      z-index: 2;
      font-size: 1.25em;
    }
    &:before{
      content: '';
      position: absolute;
      bottom: 0;
      left: -.125em;
      width: 0;
      height: .5em;
      background-color: $primary2;
      opacity: 0;
      transition: inherit;
    }
    &:hover{
      color: $black;
      &:before{
        opacity: 1;
        width: calc(100% + .25em);
      }
    }
  }
}

.burger{
  display: none;
}


@media screen and (max-width: 992px){
  .header{
    padding: 1.875em 0;
    &__inner{
      align-items: flex-end;
    }
    &__right{
      flex-direction: column-reverse;
      align-items: flex-end;
    }
  }

  .nav{
    margin-top: 1.75em;
    &__link{
      line-height: 137.5%;
      margin: 0 0 0 2.75em;
      span{
        font-size: 1em;
      }
      &:before{
        height: .4375em;
      }
    }
  }
}


@media screen and (max-width: 768px){
  .header{
    padding: 2.1875em 0 1.5em;
    &__inner{
      align-items: center;
    }
    &__right{
      margin: 0;
    }
    &._active{
      .burger{
        span{
          &:nth-child(1){
            transform: rotate(-45deg) translate(-.44em,.3em);
          }
          &:nth-child(2){
            opacity: 0;
            transform: translateX(-1em);
          }
          &:nth-child(3){
            transform: rotate(45deg) translate(-.44em,-.3em);
          }
        }
      }
      .nav{
        opacity: 1;
        pointer-events: unset;
      }
    }
  }

  .nav{
    transition: all .6s;
    opacity: 0;
    pointer-events: none;
    margin: 0;
    position: fixed;
    top: 7em;
    left: 0;
    width: 100%;
    height: calc(100vh - 7em);
    flex-direction: column;
    background-color: #fff;
    &__link{
      margin: 0 0 1em;
    }
  }

  .burger{
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    width: 1.5em;
    height: 1.125em;
    margin-left: 1.5em;
    span{
      transition: all .3s;
      height: 1px;
      width: 100%;
      display: block;
      border-radius: .625em;
      background: #000000;
    }
  }
}
