.catalog{
  padding-bottom: 6.25em;
  &__nav{
    display: flex;
    justify-content: center;
    margin-top: 3.125em;
  }
  &__more{
    height: 3.125em;
    padding: 0 1em;
    color: $black;
    &:hover{
      color: $black;
      opacity: .8;
    }
    span{
      font-size: 1.5em;
    }
    img{
      width: 1.5625em;
      display: block;
      flex-shrink: 0;
      margin-left: .625em;
    }
  }
}

@media screen and (max-width: 576px){
  .catalog{
    padding-bottom: 3.125em;
  }
}
