@import "../mixins";
.about{
  padding-bottom: 6.25em;
  &__grid{
    margin-top: 3.125em;
    display: grid;
    grid-template-columns: 38.125em auto;
    align-items: flex-end;
    gap: 6.25em;
  }
  &__img{
    img{
      width: 100%;
    }
  }
  &__content{
    max-width: 33.125em;
    .display1{
      line-height: 112%;
    }
  }
  &__desc{
    margin-top: 3.125em;
    @include grid100gap(1em);
    p{
      font-size: 1.5625em;
      line-height: 136%;
    }
  }
  &__btn{
    margin-top: 3.125em;
    width: 100%;
    height: 3.125em;
    span{
      font-size: 1.5em;
    }
  }
}


@media screen and (max-width: 992px){
  .about{
    &__grid{
      grid-template-columns: 26.875em auto;
      gap: 1.875em 1.5em;
    }
  }
}


@media screen and (max-width: 768px){
  .about{
    &__grid{
      grid-template-columns: 20.625em auto;
    }
    &__desc{
      p{
        font-size: 1em;
      }
    }
  }
}


@media screen and (max-width: 576px){
  .about{
    padding-bottom: 3.75em;
    &__img{
      height: 13.125em;
      img{
        height: 100%;
        object-fit: cover;
      }
    }
    &__grid{
      grid-template-columns:100%;
      margin-top: 1.875em;
    }
    &__title{
      text-align: center;
    }
    &__desc,
    &__btn{
      margin-top: 1.875em;
    }
  }
}
