@import "../mixins";
.faq{
  flex-grow: 1;
  padding-bottom: 5.25em;
  &__items{
    margin-top: 2em;
  }
}

.qa-card{
  border-top: 1px solid #000000;
  &._active{
    .qa-card__btn{
      img{
        transform: rotate(-90deg);
      }
    }
  }
  &__show{
    padding: 1.5em .625em 1.5em 0;
    display: flex;
    align-items: center;
    justify-content: space-between;
    cursor: pointer;
  }
  &__title{
    font-size: 1.5625em;
    line-height: 136%;
    font-weight: 700;
  }
  &__btn{
    flex-shrink: 0;
    margin-left: 1em;
    width: 1.5em;
    img{
      width: 100%;
      transition: all .3s;
    }
  }
  &__hidden{
    padding-bottom: 1.5em;
    display: none;
  }
  &__text{
    @include grid100gap(1.5em);
    & * {
      line-height: inherit;
    }
  }
}


@media screen and (max-width: 576px){
  .faq{
    padding-bottom: .5em;
  }

  .qa-card{
    &__title{
      font-size: 1em;
    }
  }
}
