@import "../mixins";
.profi-slider{
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  .swiper{
    &-container{
      height: 100%;
    }
    &-slide{
      height: 100%;
    }
  }
  &__images{
    position: relative;
    height: 100%;
    width: 100%;
  }
}

.profi{
  background: #F2EEE5;
  padding: 11% 0;
  position: relative;
  &__info{
    text-align: center;
    position: relative;
    z-index: 2;
  }
  &__desc{
    margin-top: .4em;
  }
  &__nav{
    margin: 1.25em auto 0;
    max-width: 23.4375em;
    width: 100%;
    @include grid100gap(1.25em);
    .m-btn{
      height: 3.125em;
      text-transform: uppercase;
      span{
        font-size: 1.25em;
      }
    }
  }
  &__img{
    position: absolute;
    top: 0;
    height: 100%;
    width: auto;
  }
  &__img1{
    left: 0;
  }
  &__img2{
    right: 0;
  }
}

.popular{
  padding: 6.25em 0 3.765em;
  &__title{
    text-align: center;
  }
  &__grid{
    display: grid;
    grid-template-columns: repeat(3,calc(100% / 3 - 1em));
    gap: 3.125em 1.5em;
    margin-top: 3.125em;
  }
}

.product-card{
  display: flex;
  flex-direction: column;
  &__img{
    display: block;
    margin-bottom: 1em;
    @include imgCover(106.5%);
  }
  &__info{
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-top: 1em;
  }
  &__users{
    display: flex;
    align-items: center;
  }
  &__reviews{
    margin-left: .625em;
  }
  &__text{
    margin-top: 1em;
    a{
      text-decoration: underline;
    }
  }
  &__nav{
    padding-top: 1em;
    margin-top: auto;
  }
  &__btn{
    width: 100%;
    height: 3.4375em;
    span{
      font-size: 1.5em;
    }
  }
}

.reasons{
  padding: 3.75em 0 4.375em;
  &__grid{
    margin-top: 3.125em;
    display: grid;
    grid-template-columns: 39.75em auto;
    gap: 1.625em 5em;
  }
  &__img{
    img{
      width: 100%;
      display: block;
    }
  }
  &__items{
    @include grid100gap(1.5em);
  }
  &__item{
    display: flex;
    align-items: center;
    &-num{
      width: 3.125em;
      height: 3.125em;
      border-radius: 50%;
      background-color: $primary3;
      flex-shrink: 0;
      margin-right: .875em;
      font-weight: 700;
      @include center;
      color: #fff;
      span{
        font-size: 1.75em;
      }
    }
    &-text{
      font-size: 1.5625em;
      line-height: 136%;
    }
  }
}

.info{
  padding: 3.125em 0;
  &__grid{
    display: grid;
    grid-template-columns: repeat(3,calc(100% / 3 - 2.75em / 3));
    gap: 3.125em 1.375em;
  }
}

.info-card{
  min-height: 27.5em;
  padding: 3.75em .625em;
  color: #FFFFFF;
  background-image: $redLinear;
  @include center;
  position: relative;
  overflow: hidden;
  &__img{
    position: absolute;
    top: 50%;
    left: 50%;
    height: 100%;
    transform: translate(-50%,-50%);
    width: auto;
  }
  &__info{
    position: relative;
    z-index: 2;
    width: 100%;
    text-align: center;
    @include grid100gap(1.5em);
    max-width: 23.125em;
  }
  &__nav{
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    padding: 1em;
    display: flex;
    justify-content: center;
  }
  &__link{
    display: flex;
    align-items: center;
    color: #fff;
    &:hover{
      color: #fff;
      opacity: .7;
    }
    span{
      font-size: 1em;
    }
    img{
      width: 1.5625em;
      flex-shrink: 0;
      margin-left: .75em;
    }
  }
}

.videos{
  padding: 3.125em 0;
  &__info{
    text-align: center;
    @include grid100gap(1.5em);
  }
  &__title{
    letter-spacing: -0.05em;
    line-height: 112%;
  }
  &__desc{
    width: 100%;
    margin: 0 auto;
    max-width: 56.25em;
  }
  &__slider{
    margin-top: 3.125em;
  }
  .swiper{
    &-container{
      overflow: hidden;
    }
  }
}

.video{
  display: block;
  position: relative;
  overflow: hidden;
  &__img{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    object-fit: cover;
  }
  video,
  &__img{
    width: 100%;
    display: block;
  }
  video{
    //opacity: 0;
  }
  &__play{
    //pointer-events: none;
    width: 5em;
    position: absolute;
    z-index: 2;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%);
    border-radius: 50%;
    img{
      width: 100%;
      transition: all .4s;
    }
    &:hover{
      cursor: pointer;
      img{
        transform: scale(1.1);
      }
    }
  }
  &._played{
    .video__play{
      display: none;
    }
    video{
      opacity: 1;
    }
    .video__img{
      opacity: 0;
    }
  }
}

.works{
  padding: 3.125em 0;
  &__grid{
    display: grid;
    grid-template-columns: repeat(2,calc(50% - 2em));
    gap: 4em;
    position: relative;
  }
  &__media{
    position: absolute;
    right: 0;
    top: 0;
    width: calc(50% - 2em);
    height: 42.5em;
    &-wrap{
      height: 100%;
    }
  }
  &__content{
    min-height: 42.5em;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
  }
  &__title{
    letter-spacing: -0.05em;
  }
  &__group{
    padding-top: 1em;
    @include grid100gap(1.5em);
    p{
      line-height: inherit;
    }
  }
  &__btn{
    width: 100%;
    height: 3.4375em;
    margin-top: 1em;
    span{
      font-size: 1.5em;
    }
  }
}

.science{
  padding: 3.125em 0;
  &__title{
    text-align: center;
  }
  &__slider{
    margin-top: 3.125em;
    overflow: hidden;
  }
  .swiper{
    &-container{
      width: calc(100% + 1.5em);
    }
    &-slide{
      padding-right: 1.5em;
    }
    &-pagination{
      margin-top: 1.5em;
    }
  }
}

.science-card{
  display: flex;
  flex-direction: column;
  &__img{
    display: block;
    margin-bottom: 1.75em;
    @include imgCover(82.5%);
  }
  &__title{
    font-weight: 700;
    font-size: 1.25em;
    line-height: 140%;
  }
  &__desc{
    margin-top: 1em;
  }
  &__nav{
    margin-top: auto;
    padding-top: 1em;
    display: flex;
  }
  &__link{
    text-transform: uppercase;
  }
}

.philosophy{
  padding: 3.125em 0;
  &__grid{
    display: grid;
    grid-template-columns: repeat(3,calc(100% / 3 - 2.75em / 3));
    gap: 3.125em 1.375em;
    margin-top: 3.125em;
  }
}

.philosophy-card{
  min-height: 37.5em;
  background-image: $blueLinear;
  padding: 5em 1em;
  display: flex;
  align-items: center;
  overflow: hidden;
  position: relative;
  &__slider{
    width: 100%;
    height: 100%;
  }
  .swiper{
    &-container{
      width: 100%;
      height: 100%;
    }
    &-wrapper{
      align-items: stretch;
    }
    &-slide{
      height: auto;
      min-height: 100%;
      display: flex;
      align-items: center;
      justify-content: center;
      text-align: center;
      color: #fff;
    }
    &-pagination{
      position: absolute;
      width: 100%;
      bottom: 2.5em;
      left: 0;
      z-index: 2;
      &-bullet{
        &:not(.swiper-pagination-bullet-active){
          background: #fff;
          opacity: 1;
        }
      }
    }
  }
}

.review{
  margin-top: 3.125em;
  background-color: #F2EEE5;
  background-image: url(../img/review-bg.webp);
  background-size: auto 100%;
  background-repeat: no-repeat;
  overflow: hidden;
  position: relative;
  .swiper{
    &-pagination{
      position: absolute;
      left: 0;
      bottom: 2.5em;
      width: 100%;
      z-index: 2;
    }
  }
}

.review-card{
  padding: 5.875em 0;
  &__title{
    font-size: 2.125em;
    line-height: 138%;
    font-weight: 700;
    text-align: center;
  }
  &__by{
    max-width: 23.125em;
    width: 100%;
    margin: 2.5em 0 0 auto;
    text-align: center;
    &-text{
      font-size: 1.5625em;
      line-height: 136%;
    }
  }
}


@media screen and (max-width: 992px){
  .profi{
    padding: 12.5em 0;
    &__info{
      max-width: 37.5em;
    }
    &__img1{
      display: none;
    }
    @media screen and (min-width: 577px){
      &__title{
        font-size: 4.0625em;
      }
      &__desc{
        font-size: 1.6875em;
      }
    }
  }

  .popular{
    padding-bottom: 3.125em;
    &__grid{
      grid-template-columns: repeat(2,calc(50% - 1.5625em));
      gap: 3.125em;
    }
  }

  .reasons{
    padding: 3.125em 0;
    &__grid{
      grid-template-columns: 100%;
    }
    &__img{
      width: calc(100% + 1.25em);
      margin-left: -.625em;
    }
    @media screen and (min-width: 769px){
      &__items{
        display: block;
        column-count: 2;
        column-gap: 1.25em;
      }
      &__item{
        margin-bottom: 1.25em;
      }
    }
  }

  .info{
    &__grid{
      grid-template-columns: 100%;
    }
  }

  .videos{
    &__slider{
      width: calc(100% + 1.25em);
      margin-left: -.625em;
    }
  }

  .works{
    &__grid{
      grid-template-columns: 100%;
    }
    &__title{
      text-align: center;
    }
    &__content{
      min-height: unset;
    }
    &__group{
      gap: 1em;
    }
    &__btn,
    &__group,
    &__media{
      width: 36.75em;
      max-width: 100%;
      margin-left: auto;
      margin-right: auto;
    }
    &__media{
      position: static;
      margin-top: 3.125em;
      margin-bottom: 2.125em;
      height: unset;
      &-wrap{
        position: relative;
        padding-bottom: 116%;
        .before-after{
          width: 100%;
          position: absolute;
          top: 0;
          left: 0;
        }
      }
    }
    &__btn{
      margin-top: 3.125em;
    }
  }

  .science{
    overflow: hidden;
    &__slider{
      overflow: unset;
      width: 25em;
      margin-left: auto;
      margin-right: auto;
    }
  }

  .philosophy{
    &__grid{
      grid-template-columns: 100%;
      width: calc(100% + 1.25em);
      margin-left: -.625em;
    }
  }

  .philosophy-card{
    min-height: 34.375em;
  }

  .review-card{
    padding: 8.125em 0;
    &__by{
      margin-right: auto;
      max-width: 29.375em;
    }
  }
}


@media screen and (max-width: 768px){
  .profi-slider{
    position: static;
  }

  .profi{
    padding: 0;
    &__img{
      position: static;
      width: 100%;
      height: auto;
    }
    &__inner{
      padding: 1.5em 0 2em;
    }
    &__info{
      max-width: 100%;
    }
  }

  .popular{
    &__grid{
      grid-template-columns: repeat(2,calc(50% - 1.375em));
      gap: 2em 2.75em;
    }
  }

  .info-card{
    &__title{
      font-size: 3.125em;
    }
  }

  .review-card{
    padding: 10.25em 0;
    &__title{
      font-size: 1.5em;
    }
    &__by{
      max-width: 100%;
      margin-top: 4em;
      &-text{
        font-size: 1em;
      }
    }
  }
}


@media screen and (max-width: 576px){
  .profi{
    &__title{
      font-size: 2.5em;
    }
  }

  .popular{
    padding: 1.875em 0 1.5625em;
    &__grid{
      grid-template-columns: 100%;
      margin-top: 1.875em;
    }
  }

  .reasons{
    &__grid{
      margin-top: 1.5em;
    }
    &__item{
      &-text{
        font-size: 1em;
      }
    }
  }

  .info{
    &__grid{
      gap: 1.875em;
    }
  }

  .videos{
    padding: 1.5625em 0;
    &__slider{
      margin-top: 1.875em;
    }
    .swiper{
      &-pagination{
        display: none;
      }
    }
  }

  .video{
    &__play{
      width: 1.5625em;
    }
  }

  .works{
    padding: 1.5625em 0;
    &__title{
      font-size: 32px;
    }
    &__media{
      max-width: unset;
      width: calc(100% + 1.25em);
      margin: 1.5em 0 .5em -.625em;
    }
    &__btn{
      margin-top: 2em;
    }
  }

  .science{
    padding: 1.5625em 0;
    &__slider{
      margin-top: 1.5em;
      width: 100%;
    }
  }

  .philosophy{
    padding: 1.5625em 0;
    &__grid{
      margin-top: 1.875em;
    }
  }

  .review{
    margin-top: 1.5625em;
  }

  .review-card{
    padding: 3.125em 0 8.75em;
    &__by{
      margin-top: 3.125em;
    }
  }
}
