@import 'vars';
@import 'components/_text.scss';
@import 'components/_buttons.scss';
@import "components/_inputs.scss";
.swiper-pagination{
  position: static;
  margin-top: .5em;
  font-size: 1em;
  &-bullet{
    width: 3.625em;
    height: .3125em;
    background: $black;
    opacity: .4;
    margin: 0 .4375em !important;
    border-radius: 0;
    &-active{
      opacity: 1;
    }
  }
}

.twentytwenty-wrapper.twentytwenty-horizontal{
  height: 100%;
}

.before-after{
  height: 100% !important;
  img{
    height: 100%;
    width: 100%;
    object-fit: cover;
  }
}

.twentytwenty-horizontal .twentytwenty-after-label:before,
.twentytwenty-horizontal .twentytwenty-before-label:before{
  display: none;
}

.twentytwenty-horizontal .twentytwenty-handle{
  width: 4em;
  height: 4em;
  background-color: #FFFFFF;
  background-image: url(../img/before-after.svg);
  @include bgImgSize(3.125em);
  &:before,
  &:after{
    z-index: -1;
    width: .5em;
    box-shadow: none;
    margin-left: -.25em;
  }
  &:before{
    margin-bottom: 2em;
  }
  &:after{
    margin-top: 2em;
  }
  span{
    display: none;
  }
}


._animate{
  opacity: 0;
  transition: all 1.6s;
  transform: translateY(5em);
  &._animated{
    opacity: 1;
    transform: translateY(0px);
  }
}
