@import "../mixins";
.main-slider{
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  .swiper{
    &-container{
      height: 100%;
    }
    &-slide{
      height: 100%;
    }
  }
  &__img{
    position: relative;
    height: 100%;
    width: 100%;
    img{
      position: absolute;
      top: 0;
      right: 0;
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
    ._view2,
    ._view3,
    ._view4{
      display: none;
    }
  }
}
.main{
  position: relative;
  padding: 7.45% 0;
  flex-grow: 1;
  &__inner{
    max-width: 60em;
    position: relative;
    z-index: 2;
  }
  &__title{
    color: #FFFFFF;
  }
  &__hint{
    padding: 0 .625em;
    margin-top: 2.5em;
    display: flex;
    align-items: center;
    &-icon{
      width: 2.6875em;
      height: 2.6875em;
      border-radius: 50%;
      @include center;
      background-color: $primary3;
      margin-right: .75em;
      flex-shrink: 0;
      svg{
        width: 1.5em;
        height: auto;
      }
    }
  }
  &__nav{
    margin-top: 2.75em;
    width: 50em;
    max-width: 100%;
    display: grid;
    grid-template-columns: repeat(2,1fr);
    gap: 3.125em;
    .m-btn{
      height: 3.125em;
      text-transform: uppercase;
      span{
        font-size: 1.25em;
      }
    }
  }
}

.b-marquee {
  white-space: nowrap;
  overflow: hidden;
  color: #fff;
  position: relative;
  min-height: 4em;
  display: flex;
  align-items: center;
  background: linear-gradient(90deg, #78D4A8 0%, #60A4FF 31.88%, #D169D1 63.76%, #F81D55 95.63%);
  &--black{
    color: $black;
  }
}

.b-marquee__text {
  font-size: 1.375em;
  animation: animMarquee 25s linear infinite;
  transform: translateX(-100%);
}

@keyframes animMarquee {
  0% {
    transform: translateX(-100%);
  }
  100% {
    transform: translateX(100%);
  }
}


@media screen and (max-width: 992px){
  .main-slider{
    &__img{
      ._view1{
        display: none;
      }
      ._view2{
        display: block;
      }
    }
  }

  .main{
    padding: 7.5em 0;
    &__inner{
      max-width: 38.375em;
    }
    &__title{
      line-height: 105%;
    }
    &__hint{
      margin-top: 1.25em;
    }
    &__nav{
      margin-top: 1.5em;
      grid-template-columns: 100%;
      gap: 1.5em;
      width: 23.4375em;
    }
  }
}


@media screen and (max-width: 768px){
  .main-slider{
    &__img{
      ._view2{
        display: none;
      }
      ._view3{
        display: block;
      }
    }
  }

  .main{
    padding: 9.375em 0 5.5em;
    background-image: url(../img/main-bg3.jpg);
  }
}


@media screen and (max-width: 576px){
  .main-slider{
    position: static;
    .swiper{
      &-wrapper{
        align-items: stretch;
      }
      &-slide{
        height: auto;
      }
    }
    &__img{
      position: static;
      object-fit: cover;
      img{
        position: static;
      }
    }
  }

  .main{
    padding: 0;
    background: unset;
    .container{
      padding: 0;
    }
    &__inner{
      max-width: 100%;
    }
    &__info{
      padding: 1.875em .75em 2.25em;
    }
    &__title{
      color: $black;
      text-align: center;
    }
    &__hint{
      max-width: 16.75em;
      margin: .75em auto 0;
    }
  }

  .b-marquee__text{
    font-size: 1em;
  }
}

