@import "../vars.scss";
.breadcrumbs {
    margin: 1.5em 0 1.8125em;
    &__item {
        font-size: 1.125em;
        line-height: 138%;
        position: relative;
        display: inline-flex;
        align-items: center;
        &:last-child {
            color: $primary;
        }
        &:not(:first-child) {
            &:before {
                content: '/';
                margin: 0 .66em 0 .5em;
                color: $black;
            }
        }
    }
}

@media screen and (max-width: 992px){
    .breadcrumbs{
        margin: 0 0 2em;
    }
}


@media screen and (max-width: 576px){
    .breadcrumbs{
        &__item{
            font-size: .875em;
        }
    }
}
