@import "../vars";
.m-btn {
    display: flex;
    align-items: center;
    justify-content: center;
    transition: all ease-out .225s;
    padding: 0;
    font-size: 1em;
    &:focus {
        box-shadow: none;
    }
}

.m-btn-primary{
    border-radius: .1875em;
    background-color: $primary2;
    color: $black;
    font-weight: 700;
    text-transform: uppercase;
    &:hover{
        background-color: $black;
        color: #fff;
    }
}

.m-btn-outline{
    border: 1px solid $black;
    color: $black;
    &:hover{
        background-color: $black;
        color: #fff;
    }
}

.m-btn-clack{
    background-color: $black;
    color: #fff;
    &:hover{
        background-color: $primary2;
        color: $black;
    }
}
